import React, { useState, useEffect } from 'react';
import dummydata from '../../utils/data';

import Nav from '../../components/Nav';
import DataTable from '../../components/DataTable';
import Spinner from '../../components/Spinner';
import Modal from '../../components/Modal';
import Notification from '../../components/Notification';
import Alert  from '../../components/Alert';
import ProgressBar  from '../../components/ProgressBar';

const Mandatory = () => {

  let [loading, setLoading] = useState(true);
  let [data, setData] = useState(dummydata);
  let [notification, setNotification] = useState({active: false})
  let [progress, setProgress] = useState({active: false, percentage: 0});
  let [alert, setAlert] = useState({active: false, text: '', type: '', icon: ''});

  let hiddenColums = {
    theme: false,
    program: false,
    title: false,
    format: false,
    persona: false,
    abstract: false,
    startDate: true,
    endDate: true,
    results: true,
    asset: true,
  };

  useEffect(() => {
    handleLoad();
  }, [data]);
  
  let [modal, setModal] = useState({
    active: false
  });

  const handleLoad = () => {
    setTimeout(() => {
      setLoading(false)
    }, 2000)
  }

  const addRow = (e, row) => {
    e.preventDefault();
    setData([...data, row]);
    toggleRowModal(false);
    showNotification('New row created satisfactorily.')
  }

  const editRow = (e, row, i) => {
    e.preventDefault();
    let editData = data;
    editData.splice(i, 1, row);
    setData(editData);
    toggleRowModal(false);
    showNotification('New row edited satisfactorily.');
  }

  const deleteRow = i => {
    let filterData = data.filter((row, idx) => idx !== i);
    setData(filterData);
  }

  const toggleRowModal = (active, onSubmit, values, type, idx) => {
    setModal(prev => {
      return {values, type, active, onSubmit, idx}
    });
  }

  const showNotification = (text) => {
    setNotification({active: true, text});
    setTimeout(() => {
      setNotification({active: false});
    }, 5000)
  }

  const handleAlert = (active, text, type, icon) => {
    setAlert({active, text, type, icon});
    setInterval(() => {
      setAlert({active: false, text: '', type: '', icon: ''});
    }, 7000);
  }

  const handleProgress = () => {
    setProgress({active: true});
    let interval = setInterval(() => {
      setProgress({active: true, percentage: progress.percentage++});
      if(progress.percentage === 101) {
        setProgress({active: false, percentage: 0});
        handleAlert(true, "CSV file Uploaded successfully", "success", "success");
        clearInterval(interval);
      }
    }, 20);
  }

  if(loading) {
    return <Spinner />
  } else {
    return(
      <section class="slds-grid slds-wrap">
      {modal.active && <Modal onSubmit={modal.onSubmit} values={modal.values} idx={modal.idx} type={modal.type} toggleRowModal={toggleRowModal} />}
      {notification.active && <Notification text={notification.text} />}
      <Nav handleAlert={handleAlert} addFile={handleProgress} addRow={addRow} toggleRowModal={toggleRowModal} />
      {alert.active && <Alert handleAlert={handleAlert} text={alert.text} type={alert.type} icon={alert.icon} />}
      {progress.active && <ProgressBar progress={progress.percentage} />}
      <div class="slds-grid slds-wrap slds-p-around_medium slds_full-width">
        <div class="slds-col slds-size_12-of-12">
          {
            data.length > 0 ?
              <DataTable
                toggleRowModal={toggleRowModal}
                data={data}
                deleteRow={deleteRow}
                editRow={editRow}
                hiddenCols={hiddenColums}
              /> : (
                <div className="slds-admin-view">
                  <img src="/assets/images/product4.png" alt="Salesforce character"/>
                  <h2 class="slds-text-heading_large slds-text-align_center">It looks like there's nothing here yet.</h2>
                  <div class="slds-m-vertical_medium">
                    <p class="slds-text-align_center">Let's get this party started by building your first form!</p>
                    <p class="slds-text-align_center">click below to kick things off.</p>
                  </div>
                  <ul className="slds-button-group-row slds-m-top_x-small">
                    <li className="slds-button-group-item">
                      <div className="slds-form-element__control">
                        <div className="slds-file-selector slds-file-selector_files">
                          <input onChange={handleProgress} type="file" className="slds-file-selector__input slds-assistive-text" accept="image/png" id="file-upload-input-01" aria-labelledby="file-selector-primary-label file-selector-secondary-label" />
                          <label className="slds_cursor-pointer slds-button slds-button_brand" htmlFor="file-upload-input-01" id="file-selector-secondary-label"  title="Upload CSV">
                            Upload CSV file
                          </label>
                        </div>
                      </div>
                    </li>
                    <li className="slds-button-group-item">
                      <button onClick={() => toggleRowModal(true, addRow)} className="slds-button slds-button_neutral">Create a row</button>
                    </li>
                  </ul>
                </div>
              )
          }
        </div>
      </div>
      </section>
    )
  }
}

export default Mandatory;