import React from 'react';

const Alert = ({
    text,
    type,
    icon,
    handleAlert
}) => {
  

  return(
    <div className={`slds-notify slds-notify_alert slds-theme_alert-texture slds-theme_${type}`} role="alert">
        <span className="slds-assistive-text">{type}</span>
        <span className="slds-icon_container slds-icon-utility-error slds-m-right_x-small" title={text}>
          <svg className="slds-icon slds-icon_x-small" aria-hidden="true">
            <use xlinkHref={`/assets/icons/utility-sprite/svg/symbols.svg#${icon}`} />
          </svg>
        </span>
        <h2>{text}</h2>
        <div className="slds-notify__close">
          <button onClick={() => handleAlert(false)} className="slds-button slds-button_icon slds-button_icon-small slds-button_icon-inverse" title="Close">
            <svg className="slds-button__icon" aria-hidden="true">
              <use xlinkHref="/assets/icons/utility-sprite/svg/symbols.svg#close" />
            </svg>
            <span className="slds-assistive-text">Close</span>
          </button>
        </div>
      </div>
  )
}

export default Alert;