import React from 'react';
import { Switch, Route } from 'react-router-dom';

import Login from './containers/Login';
import Admin from './containers/Admin';
import Mandatory1 from './containers/Mandatory1';
import Mandatory2 from './containers/Mandatory2';
import Mandatory3 from './containers/Mandatory3';

const App = () => {
  return (
    <Switch>
      <Route exact path='/' component={Login} />
      <Route exact path='/admin' component={Mandatory1} />
      <Route exact path='/mandatory-1' component={Mandatory1} />
      <Route exact path='/mandatory-2' component={Mandatory2} />
      <Route exact path='/mandatory-3' component={Mandatory3} />
    </Switch>
  );
}

export default App;
