import React, { Fragment, useState } from 'react';

const Modal = ({
  toggleRowModal,
  type,
  values,
  onSubmit,
  idx
}) => {

  const [row, setRow] = useState({...values});

  const handleCHange = e => setRow({...row, [e.target.name]: e.target.value});

  return(
    <Fragment>
      <section role="dialog" tabIndex={-1} aria-labelledby="modal-heading-01" aria-modal="true" aria-describedby="modal-content-id-1" className="slds-modal slds-fade-in-open">
        <div className="slds-modal__container">
          <header className="slds-modal__header">
            <button onClick={() => toggleRowModal(false)} className="slds-button slds-button_icon slds-modal__close slds-button_icon-inverse" title="Close">
              <svg className="slds-button__icon slds-button__icon_large" aria-hidden="true">
                <use xlinkHref="/assets/icons/utility-sprite/svg/symbols.svg#close" />
              </svg>
              <span className="slds-assistive-text">Close</span>
            </button>
            <h2 id="modal-heading-01" className="slds-modal__title slds-hyphenate">{type === 'edit' ? 'Edit' : 'New'} row</h2>
          </header>
          <div className="slds-modal__content slds-p-around_medium">
            <form onSubmit={e => onSubmit(e, row, idx)}>
              <div className="slds-form-element">
                <label className="slds-form-element__label">Theme</label>
                <div className="slds-form-element__control">
                  <input type="text" onChange={handleCHange} defaultValue={row.theme} className="slds-input" name="theme" />
                </div>
              </div>
              <div className="slds-form-element slds-m-top_x-small">
                <label className="slds-form-element__label">Program</label>
                <div className="slds-form-element__control">
                  <input type="text" onChange={handleCHange} defaultValue={row.program} className="slds-input" name="program" />
                </div>
              </div>
              <div className="slds-form-element slds-m-top_x-small">
                <label className="slds-form-element__label">Title</label>
                <div className="slds-form-element__control">
                  <input type="text" onChange={handleCHange} defaultValue={row.title} className="slds-input" name="title" />
                </div>
              </div>
              <div className="slds-form-element slds-m-top_x-small">
                <label className="slds-form-element__label">Format</label>
                <div className="slds-form-element__control">
                  <input type="text" onChange={handleCHange} defaultValue={row.format} className="slds-input" name="format" />
                </div>
              </div>
              <div className="slds-form-element slds-m-top_x-small">
                <label className="slds-form-element__label">Persona</label>
                <div className="slds-form-element__control">
                  <input type="text" onChange={handleCHange} defaultValue={row.persona} className="slds-input" name="persona" />
                </div>
              </div>
              <div className="slds-form-element slds-m-top_x-small">
                <label className="slds-form-element__label">Abstract</label>
                <div className="slds-form-element__control">
                  <input type="text" onChange={handleCHange} defaultValue={row.abstract} className="slds-input" name="abstract" />
                </div>
              </div>
              <div className="slds-form-element slds-m-top_x-small">
                <label className="slds-form-element__label">Region</label>
                <div className="slds-form-element__control">
                  <input type="text" onChange={handleCHange} defaultValue={row.region} className="slds-input" name="region" />
                </div>
              </div>
              <div className="slds-form-element slds-m-top_x-small">
                <label className="slds-form-element__label">Start date</label>
                <div className="slds-form-element__control">
                  <input type="date" onChange={handleCHange} defaultValue={row.startDate} className="slds-input" name="startDate" />
                </div>
              </div>
              <div className="slds-form-element slds-m-top_x-small">
                <label className="slds-form-element__label">End date</label>
                <div className="slds-form-element__control">
                  <input type="date" onChange={handleCHange} defaultValue={row.endDate} className="slds-input" name="endDate" />
                </div>
              </div>
              <div className="slds-form-element slds-m-top_x-small">
                <label className="slds-form-element__label">Results</label>
                <div className="slds-form-element__control">
                  <input type="text" onChange={handleCHange} defaultValue={row.results} className="slds-input" name="results" />
                </div>
              </div>
              <div className="slds-form-element slds-m-top_x-small">
                <label className="slds-form-element__label">Asset</label>
                <div className="slds-form-element__control">
                  <input type="text" onChange={handleCHange} defaultValue={row.asset} className="slds-input" name="asset" />
                </div>
              </div>
              <div class="slds-m-top_small">
                <button onClick={() => toggleRowModal(false)} className="slds-button slds-button_neutral">Cancel</button>
                <button type="submit" className="slds-button slds-button_brand">{type === 'edit' ? 'Edit' : 'Create'} row</button>
              </div>
            </form>
          </div>
        </div>
      </section>
      <div className="slds-backdrop slds-backdrop_open" />
    </Fragment>
  )
}

export default Modal;