import React, { useState } from 'react';

const DataRow = ({
  theme,
  program, 
  title, 
  format, 
  persona, 
  abstract, 
  region, 
  startDate, 
  endDate,
  results, 
  asset,
  index,
  deleteRow,
  editRow,
  toggleRowModal,
  hiddenCols
}) => {

  const [isClicked, setIsClicked ] = useState(false);
  const row = { theme, program, title, format, persona, abstract, region, startDate, endDate,results, asset };

  const handleClick = () => {
    setIsClicked(!isClicked);
  }

  const handleEditRow = () => {
    handleClick(false);
    toggleRowModal(true, editRow, row, 'edit', index);
  }

  return(
    <tr aria-selected="false" className="slds-hint-parent">
      { !hiddenCols.theme && 
      <th scope="row">
        <div className="slds-truncate" title={theme}>{theme}</div>
      </th>
      }
      { !hiddenCols.program && 
      <td role="gridcell">
        <div className="slds-truncate" title={title}>{program}</div>
      </td>
      }
      { !hiddenCols.title && 
      <td role="gridcell">
        <div className="slds-truncate" title={title}>{title}</div>
      </td>
      }
      { !hiddenCols.format && 
      <td role="gridcell">
        <div className="slds-truncate" title={format}>{format}</div>
      </td>
      }
      { !hiddenCols.persona && 
      <td role="gridcell">
        <div className="slds-truncate" title={persona}>{persona}</div>
      </td>
      }
      { !hiddenCols.abstract && 
      <td role="gridcell">
        <div className="slds-truncate" title={abstract}>{abstract}</div>
      </td>
      }
      { !hiddenCols.region && 
      <td role="gridcell">
        <div className="slds-truncate" title={region}>{region}</div>
      </td>
      }
      { !hiddenCols.startDate && 
      <td role="gridcell">
        <div className="slds-truncate" title={startDate}>{startDate}</div>
      </td>
      }
      { !hiddenCols.endDate && 
      <td role="gridcell">
        <div className="slds-truncate" title={endDate}>{endDate}</div>
      </td>
      }
      { !hiddenCols.results && 
      <td role="gridcell">
        <div className="slds-truncate" title={results}>{results}</div>
      </td>
      }
      { !hiddenCols.results && 
      <td role="gridcell">
        <div className="slds-truncate" title={asset}>{asset}</div>
      </td>
      }
      <td role="gridcell">
        <button onClick={handleClick} className="slds-button slds-button_icon slds-button_icon-border-filled" aria-haspopup="true" title="Show More">
          <svg className="slds-button__icon" aria-hidden="true">
            <use xlinkHref="/assets/icons/utility-sprite/svg/symbols.svg#down" />
          </svg>
          <span className="slds-assistive-text">Show More</span>
        </button>
        { isClicked && <div className="slds-dropdown slds-dropdown_right slds-dropdown_small">
          <ul className="slds-dropdown__list" role="menu" aria-label="Show More">
            <li className="slds-dropdown__item" role="presentation">
              <a onClick={handleEditRow} role="menuitem">
                <span className="slds-truncate" title="Kanban Board">Edit</span>
                <svg className="slds-icon slds-icon_x-small slds-icon-text-default slds-m-left_small slds-shrink-none" aria-hidden="true">
                  <use xlinkHref="/assets/icons/utility-sprite/svg/symbols.svg#edit" />
                </svg>
              </a>
            </li>
            <li className="slds-dropdown__item" role="presentation">
              <a onClick={() => deleteRow(index)} role="menuitem">
                <span className="slds-truncate" title="List View">Delete</span>
                <svg className="slds-icon slds-icon_x-small slds-icon-text-default slds-m-left_small slds-shrink-none" aria-hidden="true">
                  <use xlinkHref="/assets/icons/utility-sprite/svg/symbols.svg#delete" />
                </svg>
              </a>
            </li>
          </ul>
        </div>
        }
      </td>
    </tr>
  )
}

export default DataRow;