import React from 'react';

const ProgressBar = ({
  progress
}) => {
  
  return(
    <div className="slds-progress-bar" aria-valuemin={0} aria-valuemax={100} aria-valuenow={progress} role="progressbar">
      <span className="slds-progress-bar__value slds-progress-bar__value_success" style={{width: `${progress}%`}}>
        <span className="slds-assistive-text">Progress: {progress}%</span>
      </span>
    </div>
  )
}

export default ProgressBar;